import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
import uniIcons from '../../components/uni-icons/uni-icons.vue';
export default {
	components: {
		uniIcons,
	},
	data() {
		return {
			userList: [{
					icon: 'icon-08_zizhanghaoguanli',
					name: '账号管理',
					color: '#3DABFF',
					url: 'accountmange'
				},
				{
					icon: 'icon-fapiao',
					name: '开具发票',
					color: '#FF9C00',
					url: 'receipt'
				},
				{
					icon: 'icon-tianchongxing--',
					name: '账单记录',
					color: '#FFF000',
					url: 'bill'
				},
				{
					color: '#ffab9a',
					icon: 'icon-buy-fill',
					name: '购买记录',
					url: 'purchase_record'
				},
				{
					icon: 'icon-qianbao',
					name: '充值',
					color: '#2DC4B4',
					url: 'invest'
				},
				{
					icon: 'icon-juan',
					name: '现金劵',
					color: '#F8632F',
					url: 'cash'
				},
				{
					icon: 'icon-weixin',
					name: '微信改绑',
					color: '#26DD5B'
				},
				{
					icon: 'icon-fenxiang',
					name: '分享',
					color: '#26DD5B',
					url: 'share'
				}
			],
			is_bind: '',
			is_child: '',
			token: '',
			imgRemote: imgRemote,
			memberInfoData: '',
			member_default: '',
			code: ''
		};
	},
	methods: {
		memberInfo() {
			var that = this
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("memberInfo", data, (res) => {
				// console.log(res)
				if (res.data.code == 200) {
					this.memberInfoData = res.data.data.info;
					this.member_default = res.data.data.member_default;
				}
			})
		},
		myinfoPage() {
			uni.navigateTo({
				url: "/pages/user/myinfo"
			})
		},
		pageUrl(item) {
			if (item.url != 'share') {
				uni.navigateTo({
					url: `/pages/user/${item.url}`
				});
			} else {
				
			}

		},
		threePage(data) {
			if (this.token) {
				switch (data) {
					case 'recomend':
						uni.navigateTo({
							url: '/pages/index/recommed'
						});
						break;
					case 'address':
						uni.navigateTo({
							url: '/pages/shopcart/delivery'
						});
						break;
					case 'password':
						uni.navigateTo({
							url: '/pages/user/modifypwd'
						});
						break;
				}
			} else {
				uni.reLaunch({
					url: '/pages/account/login'
				});
			}
		},
		exit() {
			uni.showModal({
				title: '提示',
				content: '是否退出登录？',
				success: function(res) {
					if (res.confirm) {
						// console.log('用户点击确定');
						var that = this
						var timeStamp = Math.round(new Date().getTime() / 1000);
						var obj = {
							appid: appid,
							timeStamp: timeStamp,
						}
						var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						var data = {
							appid: appid,
							timeStamp: timeStamp,
							sign: sign,
						}
						rs.getRequests("logout", data, (res) => {
							if (res.data.code == 200) {
								uni.showToast({
									title: "退出成功",
									icon: 'none'
								})
								uni.clearStorage({
									success: function(reg) {
										uni.navigateTo({
											url: '/pages/account/login'
										});
									}
								})
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},

	},
	onLoad() {
		uni.hideTabBar();
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		var that = this;
		that.memberInfo();
		that.is_bind = uni.getStorageSync('is_bind');
		that.is_child = uni.getStorageSync("is_child");
		that.token = uni.getStorageSync("token");

	},
};
